import { Container } from "../../../../styledComponents/Container/styled";
import { HeaderCustom } from "../../../../styled";
import Image1 from "../../../../images/dragonsCapeSecond.webp";
import Image2 from "../../../../images/dragonsCape.webp";
import Advertise from "../../../../images/advertise-01.svg";
import Price from "../../Pricing";
import {
  ImageTextWrapper,
  TextWrapper,
  Image,
  Button,
  ImageMobile,
  AdMobile,
  AdMobileSecond,
  LetterPaperContainer,
  Badge,
  Figure,
  FigureMobile,
  Figcaption,
  RollerWrapper,
  Position
} from "../styled";
import { SmoothWrapper } from "../../../../styledComponents/Wrapper/styled";
import { useSelector, useDispatch } from "react-redux";
import { selectIsLightMode } from "../../../../features/DataSlice/githubSlice";
import { useEffect, useState } from "react";
import {
  fetchGithubData,
  selectLoadingState,
  setDesign,
  selectDesign,
} from "../../../../features/DataSlice/githubSlice";
import LoadingPage from "../../../../features/TechnicalTabs/LoadingPage";
import ErrorPage from "../../../../features/TechnicalTabs/ErrorPage";
import { Link } from "../../../../styledComponents/Tiles/styled";
import designbadge from "../../../../images/designTopImage.webp";

const Design = () => {
  const dispatch = useDispatch();
  const ifLoading = useSelector(selectLoadingState);
  const [isContentVisibleArray, setIsContentVisibleArray] = useState([
    false,
    false,
    false,
  ]);

  // Toggle visibility for a specific component
  const toggleContentVisibility = (index) => {
    setIsContentVisibleArray((prevArray) => {
      const newArray = [...prevArray];
      newArray[index] = !newArray[index];
      return newArray;
    });
  };

  useEffect(() => {
    dispatch(fetchGithubData());
    dispatch(setDesign());
  }, []);
  const route = useSelector(selectDesign);
  let returned = "";

  switch (ifLoading) {
    case "loading":
      returned = <LoadingPage />;
      break;
    case "success":
      returned = (
        <Container>
          <ImageTextWrapper>
            <Badge src={designbadge} />
            <TextWrapper>
              <HeaderCustom>
                Identyfikacja wizualna
              </HeaderCustom>
              <br />
              <SmoothWrapper>
                <h2>
                  Dragon <b>s</b>cape czy Dragon<b>s</b> cape?
                </h2>
                Gdy prbujesz zdecydowa si na identyfikacj wizualn Twojej
                marki musisz stan przed wieloma wyborami. Czasem drobne
                rnice mog zmieni wszystko o 180 stopni. Dlatego jestemy
                tutaj, by pomc Ci w tym trudnym zadaniu i stworzy design
                jakiego potrzebuje Twoja firma.
              </SmoothWrapper>
              <FigureMobile>
                <Image src={Image1} alt="Dragons Cape" />
                <Figcaption>"Dragons Cape"</Figcaption>
              </FigureMobile>
              <SmoothWrapper>
                <h2>Indywidualne Logo</h2>
                Stworzymy dla Ciebie unikalne logo, ktre bdzie wizytwk
                Twojej marki. Nasz proces projektowy opiera si na zrozumieniu
                Twojej firmy, jej wartoci i celw, aby wyrni Ci spord
                konkurencji.
                <h2>Kreatywne Banery i Grafiki</h2>
                Przygotujemy nie tylko logo, ale rwnie banery, grafiki, oraz
                projekty wizytwek i materiaw reklamowych, ktre przycign
                uwag Twoich klientw. Dziki kreatywnoci i zrozumieniu trendw
                projektowych, stworzymy materiay, ktre wyrni Twoj mark.
              </SmoothWrapper>{" "}
            
              <FigureMobile>
                <Image src={Image2} alt="Dragon Scape" />
                <Figcaption>"Dragon Scape"</Figcaption>
              </FigureMobile>
            
              <SmoothWrapper>
                <h2>Pozwl Wyrazi Tosamo Twojej Marki</h2>
                Nasze usugi personalizacji graficznej stron www pozwol Twojej
                witrynie internetowej mwi za Ciebie. Tworzymy spersonalizowane
                rozwizania, ktre zapewniaj optymalne dowiadczenia
                uytkownikom. Dopasowana kolorystyka i typografia nadadz
                spjnoci twojej marce.
                <p>
                  Niech Twoja marka wyrnia si w tumie! Skontaktuj si z nami
                  ju dzi, abymy mogli zacz budowa wyjtkow tosamo
                  wizualn dla Twojego biznesu.
                </p>
              </SmoothWrapper>
              <Link to="/contact">Napisz do nas!</Link>
            </TextWrapper>
            <Figure>
              <Image src={Image1} alt="Dragons Cape" />
              <figcaption>"Dragons Cape"</figcaption>
            </Figure>
          </ImageTextWrapper>

          <ImageTextWrapper>
              <Position>
            <Figure>
              <Image src={Image2} alt="Dragon Scape" />
              <figcaption>"Dragon Scape"</figcaption>
            </Figure>
            </Position>
            <TextWrapper>
              <SmoothWrapper>
                {/* <LetterPaperContainer> */}
                {/* <div
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    columnGap: "10px",
                  }}
                >
                  <h3 style={{ alignSelf: "flex-end" }}>Letter from </h3>
                  <h2 style={{ paddingBottom: "1.1px" }}> Jakub Nowakowski</h2>
                </div>
                <p style={{ fontSize: "14px", marginTop: "-13px" }}>
                  CEO Dragonscape 
                </p>  */}
                <h2>Jak wyglda proces tworzenia ? </h2>
                Zaczynamy od briefu, czyli zapytamy Ciebie np. o twoj mark,
                bran, grup docelow i to co chcesz osign. Po ustaleniu
                Twoich potrzeb wylemy faktur pro forma i zabierzemy si do
                pracy!
                <br />
                <br />
                Nastpnym elementem jest warsztat kreatywny, tzw. moodboard.
                Zaprezentujemy Tobie przykadow stylistyk logo lub caej
                identyfikacji graficznej w zalenoci od pakietu ktry
                wybierzesz. Projekt, ktry najbardziej bdzi Tobie odpowiada
                zostanie oszlifowany jak diament, po czym przedstawimy Ci
                wstpn koncepcj - gotowe propozycje.
                <br /> <br />
                To od Ciebie zaley, w ktr stron idziemy! Na kadym etapie
                moesz zdecydowa si na wdroenie poprawek do projektu. Dopiero
                po wypracowaniu satysfakcjonujcego rezultatu przygotowujemy
                finalne logo, lub inne elementy penej identyfikacji wizualnej,
                w tym ksig znaku. <br />
                <br />
                Chcesz dokona odpowiedniego wyboru? Skontaktuj si z nami ju
                dzi, abymy mogli omwi, jak moemy pomc w wizualnym
                wyraeniu Twojej marki!
              </SmoothWrapper>
              <RollerWrapper>
                <SmoothWrapper>
                  <strong
                    onClick={() => toggleContentVisibility(0)}
                    style={{ cursor: "pointer" }}
                  >
                    {isContentVisibleArray[0] ? "" : ""} Czym jest ksiga
                    znaku?
                  </strong>

                  {isContentVisibleArray[0] && (
                    <>
                      <br /> <br />
                      <p>
                        Ksiga znaku, to nic innego, jak instrukcja obsugi
                        logo, stworzona przez autora po to, by uatwi
                        prawidowe uywanie go przez inne osoby. Okrela, ktr
                        wersj logo stosowa w konkretnych syutacjach, np. na
                        ciemnym tle, jaka jest kolorystyka logo, jego minimalne
                        rozmiary, oraz jak i kiedy nie korzysta z logo lub
                        jakie s niedozwolone modyfikacje. Ma to na celu
                        zachowanie czytelnoci i spjnoci identyfikacji
                        wizualnej w kadej sytuacji.{" "}
                      </p>
                    </>
                  )}
                </SmoothWrapper>
                <SmoothWrapper>
                  <strong
                    onClick={() => toggleContentVisibility(1)}
                    style={{ cursor: "pointer" }}
                  >
                    {isContentVisibleArray[1] ? "" : ""} Co dalej?
                  </strong>

                  {isContentVisibleArray[1] && (
                    <>
                      {" "}
                      <br />
                      <br />
                      Ostatni etap naszej podry to opacenie faktury kocowej,
                      po ktrej nastpuje przekazanie Tobie praw do logo i
                      innych projektw i plikw. Po tym etapie masz ju wszystko
                      w swoich rkach!
                    </>
                  )}
                </SmoothWrapper>
                <SmoothWrapper>
                  <strong
                    onClick={() => toggleContentVisibility(2)}
                    style={{ cursor: "pointer" }}
                  >
                    {isContentVisibleArray[2] ? "" : ""} "Czy mnie na to
                    sta"?
                  </strong>

                  {isContentVisibleArray[2] && (
                    <>
                      <br />
                      <br /> Odpowiemy przewrotnie : nie sta Ci na to by NIE
                      posiada nowoczesnego i spjnego logo! Bdna komunikacja
                      wizualna odpowiada za wikszo poraek w prbach
                      przycignicia nowych klientw. Gdy chcesz stworzy
                      luksusow mark z niechlujnym logiem pozbawiasz si poowy
                      potencjalnych klientw, to samo dziaa w drug stron:
                      przyjazna i otwarta firma to nie miejsce na pospny,
                      oficjalny design!
                    </>
                  )}
                  {/* </LetterPaperContainer> */}
                </SmoothWrapper>
              </RollerWrapper>
            </TextWrapper>
          </ImageTextWrapper>
          <AdMobile src={Advertise} />
          <AdMobileSecond />
          <Price route={route} />
        </Container>
      );
      break;
    case "error":
      returned = <ErrorPage />;
      break;
    default:
      returned = <LoadingPage />;
  }

  return returned;
};

export default Design;
