// Tre komunikatu dotyczcego plikw cookies po wejciu na stron, jeli akceptacja cookies nastpuje z poziomu przegldarki: Strona wykorzystuje pliki cookies w celu jej prawidowego funkcjonowania oraz w celach korzystania z narzdzi spoecznociowych (Facebook, Instagram), analitycznych (Google Analytics), marketingowych (Piksel Facebooka). Moesz w kadej chwili okreli warunki przechowywania lub dostpu do plikw cookies we wasnej przegldarce. Jeli bdziesz korzysta z naszej strony bez zmiany w ustawieniach swojej przegldarki, bdziemy zapisywali i uzyskiwali dostp do tych informacji (cookies) w pamici Twojego urzdzenia kocowego (komputer, telefon itp.) w wymienionych celach. Wicej informacji znajdziesz w naszej Polityce prywatnoci. Czy zgadzasz si na wykorzystywanie plikw cookies? [Zgadzam si] [Polityka prywatnoci]

import CookieConsent, { Cookies } from "react-cookie-consent";

function CookieBar() {
  return (
    <>
      <CookieConsent
      disableButtonStyles={true}
      disableStyles={true}
        location="bottom"
        buttonText="Akceptuj"
        cookieName="myAwesomeCookieName2"
        // overlay
        style={{
          backgroundColor: "#202638",
          fontSize: "12px",
          lineHeight: "130%",
          padding: "20px",
          borderRadius: "10px",
          position: "fixed",
          bottom: 0,
          // width: "100%",
          zIndex: 9999,
          color: "white",
          display: "flex",
           justifyContent: "flex-start",
          margin: "10px 10px 10px 10px",
          '@media (max-width: 600px)': {
            flexDirection: "column",
            alignItems: "center",
          },
        }}
        buttonStyle={{
          color: "#FBF3E3",
          background: "#D76460",
          fontSize: "13px",
          borderRadius: "4px",
          border: "none",
          paddingBottom: "11px",
          paddingTop: "10px",
          cursor: "pointer",
          marginLeft: "10px",
          '@media (max-width: 600px)': {
            marginTop: "10px",
          },
        }}
        expires={150}
      >

        Strona wykorzystuje niezbdne pliki cookies w celu jej prawidowego
        funkcjonowania. Przyczyniaj si one do uytecznoci
        strony poprzez umoliwianie podstawowych funkcji. Strona
        internetowa nie moe funkcjonowa poprawnie bez tych ciasteczek.
        Kliknicie przycisku "Akceptuj" oznacza zgod na wykorzystywanie
        niezbdnych plikw cookie.<br/>

      </CookieConsent>
    </>
  );
}

export default CookieBar;


