import Image1 from "../../../../images/webOfferDragon.svg";
import Image2 from "../../../../images/webOfferDragonSecond.png";
import Price from "../../Pricing";
import {
  ImageTextWrapper,
  TextWrapper,
  Image,
  Button,
  PromujImg,
  Badge
} from "../styled";
import { SmoothWrapper } from "../../../../styledComponents/Wrapper/styled";
import { HeaderCustom } from "../../../../styled";
import { Container } from "../../../../styledComponents/Container/styled";
import { useSelector, useDispatch } from "react-redux";
import { selectIsLightMode } from "../../../../features/DataSlice/githubSlice";
import { useEffect } from "react";
import {
  fetchGithubData,
  selectLoadingState,
  setWeb,
  selectWeb
} from "../../../../features/DataSlice/githubSlice";
import LoadingPage from "../../../../features/TechnicalTabs/LoadingPage";
import ErrorPage from "../../../../features/TechnicalTabs/ErrorPage";
import { Link } from "../../../../styledComponents/Tiles/styled";
import promujSiebie from "../../../../images/promujSiebie.webp";
import webbadge from "../../../../images/webTopImage.png";

const Web = () => {
  const dispatch = useDispatch();
 
  const ifLoading = useSelector(selectLoadingState);
 

  useEffect(() => {
    dispatch(fetchGithubData());
    dispatch(setWeb());
  }, []);

  const route = useSelector(selectWeb);
  let returned = "";

  switch (ifLoading) {
    case "loading":
      returned = <LoadingPage/>;
      break;
    case "success":
      returned = (
        <Container>
          <ImageTextWrapper>
          <Badge src={webbadge} />
            <TextWrapper>
            <HeaderCustom>Tworzenie stron internetowych</HeaderCustom><br/>
              <SmoothWrapper>
              
                <p>
                <h2>Czy chcesz si wyrni?</h2>
                  W gszczu internetowej konkurencji marzysz o stronie, ktra nie tylko przyciga uwag, ale
                  rwnie konwertuje odwiedzajcych w lojalnych klientw?
                  Projektowanie stron internetowych to nasza pasja i
                  umiejtno, ktra przeksztaca Twoje wizje w rzeczywisto.
                </p>
                </SmoothWrapper>
                <SmoothWrapper>
                <p>
                  <h2>Spersonalizowany Design</h2> Kada strona, ktr
                  projektujemy, to efekt dogbnego zrozumienia Twojej marki.
                  Tworzymy spersonalizowane, estetyczne projekty, ktre oddaj
                  esencj Twojego biznesu.
                  <br />
                  <br />
                  <h2>Responsywno i Wysoka Wydajno</h2> W dobie
                  rnorodnoci urzdze, zapewniamy responsywne strony
                  internetowe, ktre prezentuj si doskonale na kadym ekranie.
                  Nasze rozwizania s szybkie i zoptymalizowane pod ktem
                  wydajnoci.
                  <br />
                  <br></br>
                  <h2>SEO: Optymalizacja</h2> Twoja strona nie tylko ma wyglda
                  dobrze, ale take by widoczna dla Twojej docelowej grupy
                  odbiorcw. Nasze podejcie do projektowania stron uwzgldnia
                  optymalizacj pod ktem wyszukiwarek, co pomaga w osigniciu
                  wyszej pozycji w wynikach wyszukiwania.
                 
                </p>
                </SmoothWrapper>
              <Link to="/contact">Napisz do nas!</Link>
            </TextWrapper>
            <Image src={Image1} alt="Dragons Cape" />
          </ImageTextWrapper>
          <ImageTextWrapper>
            <TextWrapper>
              <div style={{ marginBottom:"-100px" }}></div>
            <HeaderCustom>Dlaczego my ?</HeaderCustom><br/>
              <SmoothWrapper>
                <p>
                  <h2>Dowiadczenie i Pasja:</h2> Nasze dowiadczenie wyniesione z rnych bran to lata praktyki i setki
                  zadowolonych klientw. Pasja do tworzenia funkcjonalnych,
                  estetycznych stron jest w naszym DNA.
                  <h2>Indywidualne Podejcie:</h2> Nie ma dwch
                  identycznych projektw. Stawiamy na spersonalizowane podejcie
                  do kadego klienta, starajc si zrozumie i odda wizj
                  biznesow.   </p></SmoothWrapper><SmoothWrapper><p>
                  <h2>Ile to kosztuje? </h2>Jeden Rabin powie duo,
                  drugi mao. Mdry smok powie: To zaley! W dzisiejszych
                  czasach strona internetowa jest podstaw dziaania kadego
                  biznesu. Jej brak to jak zaoenie sklepu
                  modowego w rodku lasu, zamiast w centrum Mediolanu.
                  Niechlujna lub przestarzaa strona www, to zaledwie
                  postawienie znaku przy drodze: "skr w las, a odkryjesz nasz
                  sklep". Co tu nie gra prawda? Nasze ceny s atrakcyjne i
                  zaczynaj si ju od 700 z i to od Ciebie zaley, ktry pakiet wybierzesz.
                  <br /><br/>
                  Gotowy na wyjtkow obecno online dla Twojego biznesu?
                  Skontaktuj si z nami ju dzi, a razem stworzymy stron
                  internetow, ktra zapiera dech w piersiach i przycignie
                  uwag Twoich potencjalnych klientw.
                </p>
                <p>Napisz do nas lub poszukaj wicej informacji na:</p>
                <a href="https://www.promujsiebie.pl" target="_blank"><PromujImg src={promujSiebie}/></a>
                </SmoothWrapper>
            </TextWrapper>
            <Image src={Image2} alt="Dragon Scape" />
          </ImageTextWrapper>

          <Price route={route}/>
        </Container>
      );
      break;
    case "error":
      returned = <ErrorPage />;
      break;
    default:
      returned = <LoadingPage />;
  }

  return returned;
};
export default Web;
