import { Div, H1, P } from "./styled";
import { StyledList } from "../WhyUs/List/styled";

const WhyUs = () => {
  return (
    <div>
      <StyledList>
        <Div>
          <H1>Kolektyw</H1>
          <P>
            Pochodzimy z rnych bran, mamy bogate dowiadczenie zdobyte
            podczas tworzenia wielu projektw, oraz pracy w handlu. To daje nam
            rne punkty widzenia - razem tworzymy kolektyw!
          </P>
        </Div>
        <Div>
          <H1>Metody</H1>
          <P>
            Tworzymy innowacyjne projekty, ale co najwaniejsze -
            spersonalizowane i dostosowane do Twoich potrzeb ! My jestemy
            smokami , a Ty - kim chcesz by?
          </P>
        </Div>
        <Div>
          <H1>Kreatywno</H1>
          <P>
            Nie tworzymy sztampowych projektw - grafiki, logo, banery czy inne
            elementy graficzne twojej strony bd si wyrnia i tworzy spjn
            cao. Niezalenie jaki styl wybierzesz.
          </P>
        </Div>
      </StyledList>
    </div>
  );
};

export default WhyUs;
