import React from 'react';
import Pricing from './Pricing';
import { MainContainer } from './styled';

const Price = ({ route }) => {

  return (
    <MainContainer>
      <div className="pricing-component">
        <Pricing
          data={route === "design" ?
            [
              { text: "2x logo (w 2 wersjach kade)", value: true },
              { text: "Sygnet, Tagline, Logotyp", value: true },
              { text: "Warsztat Kreatywny", value: true },
              { text: "Poprawki - moliwe 3", value: true },
              { text: "Logo w formacie .png .webp .svg", value: true },
              { text: "Ksiga znaku", value: false },
              { text: "Pena identyfikacja wizualna", value: false },
            ]
            :
            [
              { text: "Landing page / wizytwka", value: true },
              { text: " Dostosowany projekt graficzny", value: true },
              { text: "   Linki do Social Media", value: true },
              { text: " Podpicie analityki Google, raporty", value: true },
              { text: "    Optymalizacja pod SEO", value: true },
              { text: "Responsywno mobilna", value: true },
              { text: " Domena, hosting, e-mail - rok gratis", value: true },
            ]
          }
          price={route === "design" ? 400 : 700 + "+"}
          currency="PLN"
          buttonContent="Skontaktuj si"
          subTitle="wietne na pocztek"
          priceText={route === "design" ? "Projekt logo z podstawow funkcjonalnoci" : "Strona www z podstawow funkcjonalnoci"}
          headerText={route === "design" ? "podstawowy" : "starter"}
          route={route}
        />
      </div>
      <div className="pricing-component">
        <Pricing
          data={route === "design" ?
            [
              { text: "2x logo (w 3 wersjach kade)", value: true },
       { text: "Sygnet, Tagline, Logotyp", value: true },
       { text: "Warsztat Kreatywny", value: true },
        { text: "Poprawki - bez limitu", value: true },
        { text: "Logo w 4 dowolnych formatach", value: true },
        { text: "Ksiga znaku - podstawowa", value: true },
       { text: "Pena identyfikacja wizualna", value: false },
            ]
            :
            [
              { text: "Do 10 podstron", value: true },
              { text: "CMS, dostosowany projekt graficzny", value: true },
              { text: "Opcjonalnie: blog, galeria zdj", value: true },
              { text: "Linki do Social Media", value: true },
              { text: "SEO, analityka Google, raporty", value: true },
              { text: "Moliwo rozbudowy witryny", value: true },
              { text: " Domena, hosting, e-mail - rok gratis", value: true },
            ]
          }
          price={route === "design" ? 800 : 1500 + "+"}
          background="linear-gradient(120deg, #8BC3C2 0%, #32937D 100%)"
          currency="PLN"
          buttonContent="Skontaktuj si"
          subTitle="dla wymagajcych"
          priceText={route === "design" ? "Ksiga znaku i moliwo poprawek bez limitu" : "Strona z zakadkami, blogiem i galeri zdj"}
          headerText={route === "design" ? "rozszerzony" : "standard"}
          route={route}
        />
      </div>
      <div className="pricing-component">
        <Pricing
          data={route === "design" ?
            [
                   { text: "Wszystko to co poprzednio ", value: true },
                    { text: "Grafiki- Facebook, LinkedIn etc.", value: true },
                    { text: "Wizytwki, materiay reklamowe", value: true },
                   { text: "Banery reklamowe", value: true },
                 { text: "1x Inny dowolny projekt", value: true },
                { text: "Ksiga znaku - rozszerzona", value: true },
                      { text: "Kolorystyka i typografia marki", value: true },
            ]
            :
            [
              { text: "indywidualna ilo podstron", value: true },
              { text: "CMS, indywidualny projekt graficzny", value: true },
              { text: "Blog, galeria zdj, sklep internetowy", value: true },
              { text: "Utworzenie Social Mediw", value: true },
              { text: "SEO, analityka Google, raporty", value: true },
              { text: "dowolna moliwo rozbudowy", value: true },
              { text: "Domena, hosting, mail - rozszerzony", value: true },
            ]
          }
          price={route === "design" ? 1300 : 3000 + "+"}
          background="linear-gradient(to left, #F1A355 0%, #D76460 100%);"
          currency="PLN"
          buttonContent="Skontaktuj si"
          subTitle={route === "design" ? "Pena kreacja marki" : "Pena moc"}
          priceText={route === "design" ? "Kompletna identyfikacja wizualna" : "Peny pakiet z moliwoci sklepu internetowego"}
          headerText={route === "design" ? "peny" : "plus"}
          route={route}
        />
      </div>
    </MainContainer>
  );
};

export default Price;