import {
  IntroBlock,
  MyPhoto,
  Text,
  Name,
  Article,
  HireMeButton,
  StyledButtonImg,
  ArrowDown,
} from "./styled";
import dragonLight from "../../images/meLight.svg";
import dragonDark from "../../images/meDark.svg";
import text from "../../images/logoWebp.webp";
import textDark from "../../images/logodark.png";
import arrowDown from "../../images/arrowDown.svg";
import { useSelector, useDispatch } from "react-redux";
import { selectIsLightMode } from "../../features/DataSlice/githubSlice";
import { Link } from "react-scroll";
import { useEffect } from "react";
import {
  fetchGithubData,
  selectLoadingState,
} from "../../features/DataSlice/githubSlice";
import LoadingPage from "../../features/TechnicalTabs/LoadingPage";
import ErrorPage from "../../features/TechnicalTabs/ErrorPage";
import { toggleBodyOverflow2 } from "../../features/DataSlice/githubSlice";

function AboutMe() {
  const dispatch = useDispatch();
  const theme = useSelector(selectIsLightMode);
  const ifLoading = useSelector(selectLoadingState);

  useEffect(() => {
    dispatch(fetchGithubData());
    dispatch(toggleBodyOverflow2());
  }, []);

  let returned = "";

  switch (ifLoading) {
    case "loading":
      returned = <LoadingPage/>;
      break;
    case "success":
      returned = (
        <>
          <IntroBlock>
            <MyPhoto
              src={theme ? dragonLight : dragonDark}
              alt="My Photo"
              themes={theme}
            />
            <Text>TO JEST</Text>
            <Name src={theme === false ? textDark : text} />
            <Article>
              Design, logo, banery, wizytwki, a nawet projektowanie i tworzenie
              stron internetowych  to nasza specjalno! W smoczej krainie
              tworzymy rozwizania,  ktre pomog Tobie sta si smokiem biznesu!
              Sprawd co dla Ciebie przygotowalimy !
            </Article>
            <HireMeButton href="mailto:kontakt@dragonscape.pl">
              <StyledButtonImg />
            </HireMeButton>
          </IntroBlock>
          <Link
            activeClass="active"
            to="WhyUsSection"
            spy={true}
            smooth={false}
            offset={-190}
            duration={850}
          >
            <ArrowDown src={arrowDown}></ArrowDown>
          </Link>
        </>
      );
      break;
    case "error":
      returned = <ErrorPage />;
      break;
    default:
      returned = <LoadingPage/>;
  }

  return returned;
}

export default AboutMe;
