import { TilesPersonSection } from "./styled";
import TilePerson from "./tilePerson.js";
import { useSelector, useDispatch } from "react-redux";
import { selectIsLightMode } from "../../features/DataSlice/githubSlice";
import { useEffect } from "react";
import {
  fetchGithubData,
  selectLoadingState,
} from "../../features/DataSlice/githubSlice";
import LoadingPage from "../../features/TechnicalTabs/LoadingPage";
import ErrorPage from "../../features/TechnicalTabs/ErrorPage";
import { SmoothWrapper } from "../../styledComponents/Wrapper/styled.js";
import { NegativeMarginTop } from "../../styledComponents/Wrapper/styled.js";

const Cast = ({ selectedCast }) => {
  const dispatch = useDispatch();
  const theme = useSelector(selectIsLightMode);
  const ifLoading = useSelector(selectLoadingState);

  useEffect(() => {
    dispatch(fetchGithubData());
  }, []);

  let returned = "";

  switch (ifLoading) {
    case "loading":
      returned = <LoadingPage/>;
      break;
    case "success":
      returned = (
        <NegativeMarginTop>
                  <h1 style={{textAlign:"center"}}>O nas</h1>
          <br />
        <SmoothWrapper>
          <p>
            Jestemy Dragonscape! Zajmujemy si projektowaniem logo i
            identyfikacji wizualnej, a take tworzeniem profesjonalnych stron internetowych
            dla rnych bran i sektorw. Nasz misj jest dostarczanie wysokiej
            jakoci usug, ktre speniaj oczekiwania naszych klientw i
            pomagaj im osign sukces w biznesie. <br />
            <br />
            Nasz firm zaoyli bracia: Miosz i Jakub, ktrzy maj
            bogate dowiadczenie w brany handlowej oraz IT. Miosz jest
            absolwentem informatyki oraz zaoycielem marki "Promuj Siebie".
            Jakub jest ekspertem do spraw stosunkw midzynarodowych (specjalno gospodarka i biznes midzynarodowy). 
            Razem postanowili poczy swoje umiejtnoci i zainteresowania, aby
            stworzy firm, ktra oferuje kompleksowe rozwizania dla potrzeb
            internetowych. W naszym zespole pracuj rwnie inni utalentowani i
            kreatywni ludzie, ktrzy dbaj o kady aspekt projektu, od analizy,
            przez projektowanie, po wdroenie i utrzymanie.
            <br />
            <br />
            Uywamy nowoczesnych technologii i narzdzi, aby zapewni naszym
            klientom szybkie, bezpieczne i funkcjonalne strony internetowe oraz
            design, ktry przyciga uwag i buduje zaufanie. Nasze portfolio
            obejmuje projekty dla rnych sektorw, takich jak gastronomia,
            turystyka, gaming, sport, czy kultura.
            Niezalenie od tego, czy potrzebujesz prostej wizytwki,
            rozbudowanego sklepu internetowego, czy oryginalnego logo, jestemy
            gotowi podj si wyzwania i dostosowa si do twoich potrzeb i
            preferencji.
            <br />
            <br />
            Jeli szukasz profesjonalnej i niezawodnej firmy, ktra stworzy dla
            ciebie stron internetow i logo, skontaktuj si z nami. Zapewniamy
            konkurencyjne ceny, terminow realizacj i gwarancj satysfakcji. Z
            Dragonscape twoja firma zyska nowy wymiar w internecie!
          </p>
          </SmoothWrapper>
          <TilesPersonSection>
            {selectedCast.map((person) => (
              <TilePerson
                key={person.id + "_" + person.job}
                personName={person.name}
                personRole={person.character}
                personId={person.id}
                personProfilePath={person.profile_path}
              />
            ))}
          </TilesPersonSection>
        </NegativeMarginTop>
      );
      break;
    case "error":
      returned = <ErrorPage />;
      break;
    default:
      returned = <LoadingPage/>;
  }

  return returned;
};

export default Cast;
